import {
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  Input,
  OnInit,
  Type,
  ViewContainerRef
} from '@angular/core';
import { DetailComponentType } from './types/detail-component.type';

@Component({
  selector: 'sof-detail',
  template: ''
})
export class DetailComponent<T> implements OnInit {
  /**
   * The component that must be dynamically injected into the template.
   */
  @Input() dynamicDetailComponent: Type<DetailComponentType<T>>;
  private componentRef: ComponentRef<DetailComponentType<T>>;
  private localTc: string;
  private localEntity: T;

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private viewContainerRef: ViewContainerRef
  ) {}

  /**
   * The translation context.
   */
  @Input() set tc(tc: string) {
    this.localTc = tc;

    if (this.componentRef) {
      (this.componentRef.instance as any).tc = this.localTc;
    }
  }

  /**
   * The entity we want to set.
   */
  @Input() set entity(entity: T) {
    this.localEntity = entity;

    if (this.componentRef) {
      (this.componentRef.instance as any).entity = this.localEntity;
    }
  }

  ngOnInit(): void {
    const factory = this.componentFactoryResolver.resolveComponentFactory(
      this.dynamicDetailComponent
    );
    this.componentRef = this.viewContainerRef.createComponent(factory);
    (this.componentRef.instance as any).tc = this.localTc;
    (this.componentRef.instance as any).entity = this.localEntity;
  }
}
